import React, { useEffect, useState } from "react";
import DeleteForeverIcon from "@mui/icons-material/DeleteForever";
import RemoveCircleOutlineIcon from "@mui/icons-material/RemoveCircleOutline";
import DoNotDisturbOnIcon from "@mui/icons-material/DoNotDisturbOn";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import EditIcon from "@mui/icons-material/Edit";
import CloseIcon from "@mui/icons-material/Close";
import SpellcheckIcon from "@mui/icons-material/Spellcheck";
import { Button, IconButton, Modal, TextField } from "@mui/material";
import { useTranslation } from "react-i18next";

function Words({ words, setWords }) {
	const [learnedWords, setLearnedWords] = useState([]);
	const [open, setOpen] = useState(false);
	const [editWord, setEditWord] = useState("");
	const [editTranslate, setEditTranslate] = useState("");

	const [oldWord, setOldWord] = useState("");
	const [oldTranslate, setOldTranslate] = useState("");
	const { t, i18n } = useTranslation();

	useEffect(() => {
		setLearnedWords(JSON.parse(localStorage.getItem("learned-words")) || []);
	}, []);

	function removeWord(word) {
		//    localStorage
		const words = JSON.parse(localStorage.getItem("words")) || [];

		//  ,     
		const updatedWords = words.filter(item => {
			const key = Object.keys(item)[0];
			return key !== word;
		});

		//      localStorage
		localStorage.setItem("words", JSON.stringify(updatedWords));
		setWords(updatedWords);
	}

	function removeGelearnteWord(word) {
		//    localStorage
		const words = JSON.parse(localStorage.getItem("learned-words")) || [];

		//  ,     
		const updatedWords = words.filter(item => {
			const key = Object.keys(item)[0];
			return key !== word;
		});

		//      localStorage
		localStorage.setItem("learned-words", JSON.stringify(updatedWords));
		setLearnedWords(updatedWords);
	}

	function learnedWord(word, translate) {
		const learnedWord = JSON.parse(localStorage.getItem("learned-words")) || [];

		if (learnedWord.length) {
			learnedWord.push({ [word]: translate });
			localStorage.setItem("learned-words", JSON.stringify(learnedWord));
			setLearnedWords(learnedWord);
		} else {
			localStorage.setItem(
				"learned-words",
				JSON.stringify([{ [word]: translate }])
			);
			setLearnedWords([{ [word]: translate }]);
		}

		removeWord(word);
	}

	const handleOpen = () => {
		setOpen(true);
	};
	const handleClose = () => {
		setOpen(false);
	};

	function handleEditWord(word, translate) {
		handleOpen();

		setOldWord(word);
		setOldTranslate(translate);
		setEditWord(word);
		setEditTranslate(translate);
	}

	function updateArray(array, wort, translate, editWord, editTranslate) {
		return array.map(item => {
			let updatedItem = {};
			for (let key in item) {
				let newKey = key === wort ? editWord : key;
				let newValue = item[key] === translate ? editTranslate : item[key];
				updatedItem[newKey] = newValue;
			}
			return updatedItem;
		});
	}

	function saveEdit() {
		const newArr = updateArray(
			words,
			oldWord,
			oldTranslate,
			editWord,
			editTranslate
		);

		localStorage.setItem("words", JSON.stringify(newArr));
		setWords(newArr);
		setEditTranslate("");
		setEditWord("");

		setTimeout(() => {
			handleClose();
		}, 500);
	}

	return (
		<div className="words">
			{words.map(item => {
				return (
					<div className="words__content" key={Object.keys(item)[0]}>
						<div className="words__main">
							<>
								<h2>
									{Object.keys(item)[0]} &nbsp;
									<IconButton
										onClick={() =>
											handleEditWord(
												Object.keys(item)[0],
												Object.values(item)[0]
											)
										}
										aria-label="edit"
									>
										<EditIcon />
									</IconButton>
								</h2>
								<h3>{Object.values(item)[0]}</h3>
							</>
						</div>
						<Modal
							open={open}
							onClose={handleClose}
							aria-labelledby="child-modal-title"
							aria-describedby="child-modal-description"
							BackdropProps={{
								style: {
									backgroundColor: "rgba(0, 0, 0, 0.3)",
									boxShadow: "none",
								},
							}}
						>
							<div className="modal">
								<div className="modal__header">
									<IconButton aria-label="clsoe" onClick={handleClose}>
										<CloseIcon />
									</IconButton>
								</div>
								<div className="modal__body">
									<TextField
										id="edit-word"
										label={t("input-placeholder-word")}
										variant="standard"
										value={editWord}
										onChange={e => setEditWord(e.target.value)}
									/>
									<TextField
										id="edit-translate"
										label={t("input-placeholder-translate")}
										variant="standard"
										value={editTranslate}
										onChange={e => setEditTranslate(e.target.value)}
									/>
								</div>
								<Button startIcon={<EditIcon />} onClick={saveEdit}>
									{t("modal-confirm-btn")}
								</Button>
							</div>
						</Modal>
						<div className="words__right">
							<div className="words__delete">
								<Button
									startIcon={<DeleteForeverIcon />}
									onClick={() => removeWord(Object.keys(item)[0])}
								>
									{t("delete-word-btn")}
								</Button>
							</div>
							<div className="words__learned">
								<Button
									startIcon={<CheckCircleIcon />}
									onClick={() =>
										learnedWord(Object.keys(item)[0], Object.values(item)[0])
									}
								>
									{t("learned-word-btn")}
								</Button>
							</div>
						</div>
					</div>
				);
			})}
			<h1 className="words__title">
				<SpellcheckIcon />
				&nbsp;&nbsp;
				{t("word-title")}
			</h1>
			{learnedWords.length ? (
				learnedWords.map(item => (
					<>
						<div className="words__content" key={Object.keys(item)[0]}>
							<div className="words__main">
								<h2>{Object.keys(item)[0]}</h2>
								<h3>{Object.values(item)[0]}</h3>
							</div>
							<div className="words__right">
								<div className="words__delete">
									<Button
										startIcon={<RemoveCircleOutlineIcon />}
										onClick={() => removeGelearnteWord(Object.keys(item)[0])}
									>
										{t("delete-word-btn")}
									</Button>
								</div>
							</div>
						</div>
					</>
				))
			) : (
				<div className="words__no-words">
					<DoNotDisturbOnIcon />
					{t("no-words-text")}
				</div>
			)}
		</div>
	);
}

export default Words;
