import { Button } from "@mui/material";
import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import { NavLink } from "react-router-dom";

function Lernen() {
	const words = JSON.parse(localStorage.getItem("words"));
	const wordsNative = words.map(item => Object.keys(item)[0]);
	const wordsTranslate = words.map(item => Object.values(item)[0]);
	const [level, setLevel] = useState(0);
	const { t, i18n } = useTranslation();

	const [finished, setFinisched] = useState(false);

	const countWords = wordsNative.length;

	const [shuffledButtons, setShuffledButtons] = useState([]);
	const [randomNumbersNative, setRandomNumbersNative] = useState(
		generateUniqueRandomIndexes(countWords, countWords)
	);

	const [mainWord, setMainWord] = useState("");

	const [buttonColor, setButtonColor] = useState("primary"); //  'primary'   

	useEffect(() => {
		createLevel();
	}, [level]);

	useEffect(() => {
		setFinisched(false);
	}, []);

	useEffect(() => {
		console.log(finished);
	}, [finished]);

	function generateUniqueRandomIndexes(
		countWords,
		countIndexes,
		excludedIndex = null
	) {
		const indexes = new Set();

		while (indexes.size < countIndexes) {
			let randomIndex;
			do {
				randomIndex = Math.floor(Math.random() * countWords);
			} while (randomIndex === excludedIndex || indexes.has(randomIndex));

			indexes.add(randomIndex);
		}

		return Array.from(indexes);
	}

	//    
	const shuffleArray = array => {
		let shuffledArray = [...array];
		for (let i = shuffledArray.length - 1; i > 0; i--) {
			const j = Math.floor(Math.random() * (i + 1));
			[shuffledArray[i], shuffledArray[j]] = [
				shuffledArray[j],
				shuffledArray[i],
			];
		}
		return shuffledArray;
	};

	const createLevel = () => {
		if (countWords > 4) {
			const arr = words.find(item => {
				return item[wordsNative[randomNumbersNative[level]]]
					? item[wordsNative[randomNumbersNative[level]]]
					: "";
			});
			let rightTranslate;

			if (arr) {
				rightTranslate = Object.values(arr)[0];
			}

			if (!rightTranslate) {
				setFinisched(true);

				return false;
			}

			const randomNumbersTranslate = generateUniqueRandomIndexes(
				countWords,
				3,
				randomNumbersNative[level]
			);

			const wrongAnswer = e => {
				const target = e.target;

				if (target) {
					target.classList.add("wrong");
					setTimeout(() => {
						target.classList.remove("wrong");
					}, 1500); // 1000  = 3 
				}
			};

			const correctAnswer = e => {
				const target = e.target;

				if (target) {
					target.classList.add("correct");
					setTimeout(() => {
						target.classList.remove("correct");
					}, 700); // 1000  = 3 
				}
			};

			//     JSX 
			const buttons = [
				<Button
					onClick={wrongAnswer}
					color={buttonColor}
					fullWidth
					variant="contained"
					className="lernen__answer-button"
					key={1}
				>
					{wordsTranslate[randomNumbersTranslate[0]]}
				</Button>,
				<Button
					onClick={wrongAnswer}
					fullWidth
					variant="contained"
					className="lernen__answer-button"
					key={2}
				>
					{wordsTranslate[randomNumbersTranslate[1]]}
				</Button>,
				<Button
					onClick={wrongAnswer}
					fullWidth
					variant="contained"
					className="lernen__answer-button"
					key={3}
				>
					{wordsTranslate[randomNumbersTranslate[2]]}
				</Button>,
				<Button
					fullWidth
					variant="contained"
					className="lernen__answer-button"
					key={4}
					onClick={e => {
						correctAnswer(e);

						setTimeout(() => setLevel(level + 1), 1000);
					}}
				>
					{rightTranslate}
				</Button>,
			];

			setShuffledButtons(shuffleArray(buttons));
		}

		setMainWord(wordsNative[randomNumbersNative[level]]);
	};

	return (
		<div className="_container">
			<section className="lernen">
				<div className="lernen__content">
					<h2 className="lernen__title">{t("learne-title")}</h2>
					<div className="lernen__body">
						{finished ? (
							<div className="lernen__finished">
								<h2 className="lernen__finished-title">
									{t("learne-text-finish1")} <br />
									{t("learne-text-finish2")}
									<span>{countWords}</span>
									{t("learne-text-finish3")}
								</h2>
								<div className="lernen__finished-group">
									<Button variant="contained">
										<NavLink to="/" className="lernen__finished-nav">
											{t("home-link")}
										</NavLink>
									</Button>
									<Button variant="contained">
										<NavLink
											to="/lernen"
											className="lernen__finished-nav"
											onClick={() => window.location.reload()}
										>
											{t("learn-nav")}
										</NavLink>
									</Button>
								</div>
							</div>
						) : countWords > 4 ? (
							<>
								<div className="lernen__count-word">
									{level + 1} <span>/</span> {countWords}
								</div>
								<div className="lernen__word">{mainWord}</div>
								<div className="lernen__translates">
									{shuffledButtons.map((button, index) => (
										<div className="lernen__button" key={index}>
											{button}
										</div>
									))}
								</div>
							</>
						) : (
							<p>{t("must-5wort")}</p>
						)}
					</div>
				</div>
			</section>
		</div>
	);
}

export default Lernen;
