import React, { useEffect, useState } from "react";
import Words from "../components/Words";

function Home() {
	const [words, setWords] = useState(JSON.parse(localStorage.getItem("words")));

	useEffect(() => {
		const words = JSON.parse(localStorage.getItem("words"))?.length;

		if (!words) {
			localStorage.setItem(
				"words",
				JSON.stringify([
					{ schlect: "" },
					{ fuchs: "" },
					{ denken: "" },
					{ angenehm: "" },
					{ "das Essen": "" },
				])
			);

			setWords([
				{ schlect: "" },
				{ fuchs: "" },
				{ denken: "" },
				{ angenehm: "" },
				{ "das Essen": "" },
			]);
		}
	}, []);

	return (
		<main className="_container main">
			<section className="hero">
				{!words ? (
					"Nie masz jeszcze adnego sowa"
				) : (
					<Words words={words} setWords={setWords} />
				)}
			</section>
		</main>
	);
}

export default Home;
